'use client';

import { Paper, Text, Stack, Group, Button } from '@mantine/core';
import { IconCreditCard, IconEye, IconEyeOff } from '@tabler/icons-react';
import { useState } from 'react';
import styles from './Cards.module.css';

export default function Cards() {
  const [isCardVisible, setIsCardVisible] = useState(true);

  const toggleCardVisibility = () => {
    setIsCardVisible(!isCardVisible);
  };

  return (
    <div className={styles.cardsWrapper}>
      <Paper shadow="lg" radius="lg" className={styles.cardContainer}>
        <div className={styles.cardHeader}>
          <Group justify="space-between" align="center" w="100%">
            <Text className={styles.cardTitle}>Банковские карты</Text>
            <Button
              size="xs"
              variant="subtle"
              color="gray"
              leftSection={isCardVisible ? <IconEyeOff size={14} /> : <IconEye size={14} />}
              onClick={toggleCardVisibility}
            >
              {isCardVisible ? 'Скрыть' : 'Показать'}
            </Button>
          </Group>
        </div>

        <div className={styles.cardContent}>
          <div className={styles.bankCard}>
            <div className={styles.cardTop}>
              <Group justify="space-between" align="flex-start">
                <div>
                  <Text className={styles.cardBank}>MultiBank</Text>
                  <Text className={styles.cardType}>Premium</Text>
                </div>
                <IconCreditCard size={32} color="#ffffff" />
              </Group>
            </div>
            
            <div className={styles.cardMiddle}>
              <Text className={styles.cardNumber}>
                {isCardVisible ? '**** **** **** 1234' : '•••• •••• •••• ••••'}
              </Text>
            </div>
            
            <div className={styles.cardBottom}>
              <Group justify="space-between" align="flex-end">
                <div>
                  <Text className={styles.cardLabel}>Владелец</Text>
                  <Text className={styles.cardValue}>
                    {isCardVisible ? 'IVAN IVANOV' : '•••• ••••••'}
                  </Text>
                </div>
                <div>
                  <Text className={styles.cardLabel}>Срок действия</Text>
                  <Text className={styles.cardValue}>
                    {isCardVisible ? '12/28' : '••/••'}
                  </Text>
                </div>
              </Group>
            </div>
          </div>

          <Stack gap="sm" mt="md">
            <Button variant="light" fullWidth size="sm">
              Управление картами
            </Button>
            <Button variant="outline" fullWidth size="sm">
              История операций
            </Button>
          </Stack>
        </div>
      </Paper>
    </div>
  );
}